
import Picker from 'vux/src/components/picker/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
components: {
Picker,
PopupPicker
},
data(){
return {
account: '',
pass: '12345678',
pickerList:[{
name: '请选择管理角色',
value: "0"
},{
name: '党支部书记',
value: "3"
}, {
name: '片区负责人',
value: "2"
}],
pickerValue: ["0"],
roleName : "请选择管理角色",
PickerVisible:false
}
},
methods:{
login() {
if (this.pickerValue[0] == "0")
return this.$vux.toast.show({
text: '请选择角色',
type: 'text',
position: 'top'
});
if (!this.account)
return this.$vux.toast.show({
text: '请输入帐号',
type: 'text',
position: 'top'
});
if (!this.pass)
return this.$vux.toast.show({
text: '请输入密码',
type: 'text',
position: 'top'
});
this.$store.dispatch('login', {
name: this.account,
password: this.pass,
openId: this.$route.query.openId || undefined,
userType : this.pickerValue[0]
})
.then(
result =>{
if(result.status){
var roleId = result.entry.roleId;
if(roleId == 2)
{
this.$router.push({
path: 'points/review'
})
}
else if(roleId == 3)
{
this.$router.push({
path: 'points/evaluation'
})
}
else
{
this.$router.push({
path: '/main' + roleId
})
}

}else{
this.$vux.toast.show({
text: result.message,
width: '18em',
type: 'text',
position: 'top'
})
}
}
)
},
keyUpReturn()
{
this.login();
},
roleChange(value){
this.pickerValue = value;
if(value == "3")
{
this.roleName = this.pickerList[1].name;
}
else if(value == "2")
{
this.roleName = this.pickerList[2].name;
}
else
{
this.roleName = this.pickerList[0].name;
}
}
},
mounted(){

}
}



